export class ListaVoltas {

    constructor() {
        
        this._voltas = [];
    }

    get voltas() {
        
        return [].concat(this._voltas);
    }
    
    adiciona(volta) {
        
        this._voltas.push(volta);
    }

    esvazia() {
        
        this._voltas = [];
    }

    ordena(criterio) {

        this._voltas.sort(criterio);        
    }
    
    inverteOrdem() {

        this._voltas.reverse();
    }    
}